import React, { Component } from 'react';
import cx from 'classnames';

import './card.scss';
// import SearchBar from '../SearchBar';

class Card extends Component {
  constructor(props) {
    super(props);
    this.state = {
      height: 311
    };
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    this.upateHeight();
  }

  createRef = (r) => {
    this.ref = r;
    this.upateHeight();
  }

  upateHeight = () => {
    if (!this.ref) return;
    // const { width } = this.ref.getBoundingClientRect();
    // const height = width * 1.035;
    // this.setState({ height });
  }

  render() {
    const { height } = this.state;
    const { name, organization, location, image_link: imageLink, dark } = this.props;

    return (
      <div className={cx("xcard", {
        "xcard--dark": dark
      })}>
        <div
          ref={this.createRef}
          className="xcard__image"
          style={{
            height: `${height}px`,
            backgroundImage: `url('${imageLink}')`
            // backgroundImage: `url('/images/causes/Breakfast Drop-in Assistant.jpg')`
          }}
        />
        <div className="xcard__info">
          <h1>{name}</h1>
          <h2>{organization}</h2>
          <div className="xcard__location">
            <div className="xcard__location-icon" />
            <h3>{location}</h3>
          </div>
        </div>
      </div>
    );
  }
}


export default Card;
